}
.service-sub-header {
    font-size: 14px;
    margin: 0 20px;
}
.service-time {
    font-size: 14px;
    font-weight: bold;
}
.service-content {
    padding-left: 5px;
}
.md-content img {
    max-width: 100%;
}
.service-tag {
    margin-left: 10px;
}
.progress {
    display: inline-block;
    width: 20%;
    margin-left: 10px;
    vertical-align: top;
}



